.login-section {
  -webkit-border-radius: 5px;
  border-radius: 5px;
  -moz-border-radius: 5px;
  background-clip: padding-box;
  margin: 180px auto;
  width: 350px;
  padding: 35px 35px 15px 35px;
  background: #fff;
  border: 1px solid #eaeaea;
  box-shadow: 0 0 25px #cac6c6;
  height: auto;
}
.login-section .heading {
  margin: 0px auto 40px auto;
  text-align: center;
  color: #505458;
}
.login-section .user-ops {
  float: right;
}
.login-section .user-ops .user-op {
  color: #1f2d3d;
}
.login-section .user-ops .user-op:hover {
  color: #35b5ff;
}
.login-section .login-btns {
  text-align: center;
}
